.form{
  display: flex;
  flex-direction: column;

  width: 400px;
  margin:0 auto;
  padding: 50px 10px 0px 10px;

  *{
    margin: 10px;
  }

  &__btn{
    width: 100%;
  }

  &__title{
    text-align: center;
    font-size: 32px;
    color: #555555
  }
}

.error{
  color: #ff2204;
  text-align: center;
  font-size: 24px;
}
