.header{
  display: flex;
  justify-content: space-between;

  &__logo{
    margin: 5px 30px;
    width: 60px;
    height: 60px;
  }

  &__btn-gryp{
    margin: 5px;
  }

  &__input{
    margin: 0 20px;
    padding: 5px;
    width: 100px;
    font-size: 18px;
    text-align: center;
    border-radius: 4px;
  }

  &__title{
    margin: 10px;
    font-size: 32px;
    line-height: 32px;
    text-transform: uppercase;
  }
}

.mainSection{
  width: 100%;
  min-height: calc(100vh - 94px);

  justify-content: space-around;
  display: inline-flex;
  flex-wrap: wrap;

  >*{
    background-color: #f5f5f5;
    border:1px solid #121212;

    margin: 10px;
  }


  .gameField{
    min-width: 576px;
    min-height: 500px;

    max-width: 750px;
    max-height: 750px;

    width: 70%;

    position: relative;
    user-select: none;

    &__warning{
      color: #ff6f00;

      font-size: 32px;
      text-align: center;
      line-height: 50px;
    }
  }

  .table{
    min-width: 250px;
    min-height: 100px;
    max-height: 750px;

    width: 20%;
    margin: 10xp;

    text-align: center; 
    font-size: 18px;
    line-height: 16px;

    user-select: none;
  }
}

.rotation{
  border-radius: 50%;
}

.cube{
  position: absolute;
  text-align: center;
  
  display: inline-block;
}

.blue{
  background-color: #0000ff;
  color: #f5f5f5;

  width: 15px;
  height: 15px;

  font-size: 12px;
}

.green{
  background-color: #00ff00;
  color: #ff0000;

  width: 20px;
  height: 20px;

  font-size: 15px;
}

.red{
  background-color: #ff0000;
  color: #f5f5f5;
  
  width: 10px;
  height: 10px;

  font-size: 8px;
}


.final{
  z-index: 9999999;
  text-align: center;
  position: fixed;

  top:0;
  left: 0;
  right: 0;
  bottom: 0;

  width: 100%;
  height: 100vh;

  background-color: rgba(10,10,10,.4);
  
  &__body{
    border: 1px solid;
    border-radius: 3px;
    margin: 50px;

    display: inline-block;

    width: 350px;
    height: 250px;

    background-color: #f5f5f5;
    >*{
      margin: 40px;
    }
  }
}